import React, {useEffect, useState} from "react";
import {
    Alert,
    Modal,
    StyleSheet,
    Text,
    Pressable,
    View,
    Button,
    FlatList,
    Platform,
    TouchableOpacity,
    SafeAreaView,
    Dimensions
} from "react-native";
import {THEME} from "../../../theme";
import {Entypo, Ionicons, MaterialCommunityIcons, MaterialIcons} from '@expo/vector-icons';
import {useDispatch, useSelector} from "react-redux";
import {TypeDataReturnRender} from "../../TypeDataReturnRender";

import { Feather } from '@expo/vector-icons';
import {Select} from "../../../templateComponents/Select/Select";
import {setModalStatus,setServiceFee} from "../../../store/actions/post";
import {SelectDataDelivery} from "./ModalSelectDataDelivery";
import {SelectTimeDelivery} from "./ModalSelectTimeDelivery";
import {ServerDB} from "../../../serverDb";

export const RecivingOrder = ({navigation, route})=>{
    const dispatch = useDispatch();

    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);
    const ServiceFee = useSelector(state => state.post.ServiceFee);
    const [awaitingTime,setAwaitingTime] =useState(false);

    const DeliveryAdresSystem = useSelector(state => state.post.adressSelect);
    const AffiliateAdresSystem = useSelector(state => state.post.affiliateSelect);

    const [timeDelivered,setTimeDelivered] = useState(0);

    const handleSetStatusModal = (nameModal,status)=>{
        dispatch(setModalStatus(nameModal,status));
    };

    const DateValueShow = useSelector(state=>state.post.preorderData.dateDelivery);
    const CityData = useSelector(state => state.post.cityData);
    const [activeTimeSHow,setactiveTimeSHow] = useState(false);

    // console.log("}{m",AffiliateAdresSystem,DeliveryAdresSystem)

    const fetchTimeDelivered = async ()=>{
        let typeDelivereds = 1; //По дефолту самовывоз
        let idAddress1 = 0;
        if(TypeDeliverySystem === true){
            typeDelivereds = 0;
            idAddress1 = DeliveryAdresSystem?.id;
        }else{
            idAddress1 = AffiliateAdresSystem?.id;
        }
        const timeDeliveredData = await ServerDB.getTimeDeliveryPoints(idAddress1,typeDelivereds,CityData.cityId);
        if(TypeDeliverySystem === true) {
            await setTimeDelivered(timeDeliveredData.timeDelivery);
            await setactiveTimeSHow(true);
            console.log('Сервисный сбор = ',timeDeliveredData.serviceFee);
            await dispatch(setServiceFee(timeDeliveredData.serviceFee));
            // console.log('Данные тут ',timeDeliveredData.timeDelivery);
        }else{
            await setTimeDelivered(timeDeliveredData.timeDelivery);
            await setactiveTimeSHow(true);
            console.log('Сервисный сбор = ',timeDeliveredData.serviceFee);
            await dispatch(setServiceFee(timeDeliveredData.serviceFee));
            // console.log('Данные тут ',timeDeliveredData.timeDelivery);
        }

    }

    useEffect(()=>{
        fetchTimeDelivered();
    },[dispatch,timeDelivered])



    let DeliveryAdres = '';
    let PickupAdres = '';

    useEffect(()=>{
        let timeToAdd=0;
        let date= new Date();
        if(TypeDeliverySystem=== true){
            //Время для доставки
            // timeToAdd=DeliveryAdresSystem.diliveryData?.timeDelivery*60000;
            timeToAdd=timeDelivered*60000;
            date=date.valueOf()+timeToAdd;
            let result=new Date(date);//дата из миллисекунд в обычную

            let Hours = result.getHours()<10 ? '0'+result.getHours() : result.getHours();
            let Minutes = result.getMinutes()<10 ? '0'+result.getMinutes() : result.getMinutes();

            let returnResult=""+Hours+":"+Minutes; // сборка времени для возврата
            setAwaitingTime(returnResult);
            // console.log('33',awaitingTime,timeDelivered,result,date,returnResult)
        }else {
            //Время ожидания заказа при самовывозе
            // timeToAdd= AffiliateAdresSystem?.timePickupReal*60000
            timeToAdd= timeDelivered*60000
            date=date.valueOf()+timeToAdd;
            let result=new Date(date);//дата из миллисекунд в обычную

            let Hours = result.getHours()<10 ? '0'+result.getHours() : result.getHours();
            let Minutes = result.getMinutes()<10 ? '0'+result.getMinutes() : result.getMinutes();
            let returnResult=""+Hours+":"+Minutes; // сборка времени для возврата
            setAwaitingTime(returnResult);
            // console.log('34',awaitingTime,AffiliateAdresSystem.timePickupReal,result,date)
        }
    },[DeliveryAdresSystem,AffiliateAdresSystem,timeDelivered]);


    if(DeliveryAdresSystem === null){
        //setDeliveryAdress('Белгород, ул. Садовая, 67');
        DeliveryAdres = 'Адрес не выбран';
    }else {
        DeliveryAdres = <>
            <TypeDataReturnRender data={DeliveryAdresSystem.city} type="" separator={false}/>
            <TypeDataReturnRender data={DeliveryAdresSystem.street} type="ул. " separator=", "/>
            <TypeDataReturnRender data={DeliveryAdresSystem.home} type="д. " separator=", " linebreak={false}/>
            <TypeDataReturnRender data={DeliveryAdresSystem.podezd} type="под. " separator=", " />
            <TypeDataReturnRender data={DeliveryAdresSystem.kvartira} type="кв. " separator=","/>
        </>
    }

    if(AffiliateAdresSystem === null){
        PickupAdres = 'Точка не выбрана';
    }else{
        //console.log(AffiliateAdresSystem);
        PickupAdres = <>
            <TypeDataReturnRender data={AffiliateAdresSystem.title} type="" separator={false}/>
        </>;
    }

    const openAdressHandler = AdressList => {
        //console.log('Открываем товар');
        handleSetStatusModal("MODAL_ORDER", false);
        window.location.hash = 'AdressList';
        navigation.navigate('AdressList', {
            productId: AdressList.id,
            title: AdressList.title,
            booked: AdressList.booked,
            productData:AdressList
        });
    };
    const openAffiliateHandler = AdressList => {
        //console.log('Открываем товар');
        handleSetStatusModal("MODAL_ORDER", false);
        window.location.hash = 'AffiliateList';
        navigation.navigate('AffiliateList', {
            productId: AdressList.id,
            title: AdressList.title,
            booked: AdressList.booked,
            productData:AdressList
        });
    };

    const DeliveryAdresBlock =
        <TouchableOpacity
            style={{flexDirection:"row",justifyContent:"space-between",alignItems:"center",width:"100%"}}
            onPress={
                ()=>openAdressHandler([{id:1,title:"Сменить адрес доставки",booked:false}])
            }
        >
            <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"95%",paddingBottom:5}}>
                <MaterialCommunityIcons name="truck-fast" size={24} color="black" />
                <Text style={styles.textIconCenter}>{DeliveryAdres}</Text>
            </View>
            {/*<View style={styles.arrowRight}><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>*/}
        </TouchableOpacity>
    ;


    const PickupAdresBlock =
        <TouchableOpacity
            style={{flexDirection:"row",justifyContent:"center",alignItems:"center",width:"100%"}}
            onPress={
                ()=>openAffiliateHandler([{id:1,title:"Сменить адрес самовывоза",booked:false}])
            }
        >
            <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"95%"}}>
                <Entypo name="shop" size={24} color="black" />
                <Text style={styles.textIconCenter}>{PickupAdres}</Text>
            </View>
            <View style={styles.arrowRight}><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
        </TouchableOpacity>

    ;


    const SelectOption = ()=>{
        let showJ=awaitingTime;
        let ShowTime= '';
        if(activeTimeSHow){
            ShowTime="Ориентировочно до "+showJ+" ± 20 мин";
        }else{
            ShowTime="Как можно скорее";
        }
        return (<SelectDataDelivery
            defaultValue={0}
            fontSize={12}
            keys="DeliveryTime"
            item={[
                {title:ShowTime,value:0},
            ]}
            NowTime={ShowTime}
        />)
    };

    const SelectOption2 = ()=>{
        return (<SelectTimeDelivery
            defaultValue={0}
            fontSize={12}
            keys="DeliveryTime"
            item={[
                {title:"Выберите время",value:0},
            ]}
        />)
    };

    const DeliveryTime =
        <TouchableOpacity
            style={{flexDirection:"row",justifyContent: 'space-between',alignItems:"center",width:"100%",}}
            onPress={
                ()=>{
                    // handleSetStatusModal("MODAL_ORDER", false);
                    // navigation.navigate('SelectDataDeliverySet');
                }
            }
        >
            <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"95%"}}>
                <Entypo name="calendar" size={24} color="black" />
                {/*<Text style={styles.textIconCenter}>Как можно скорее</Text>*/}
                {SelectOption()}
                {/*{DateValueShow === 0 && TypeDeliverySystem ?<Text style={{fontSize:12}}>Примерное время доставки: {awaitingTime.hour} часов и {awaitingTime.min} минут</Text> : false}*/}
            </View>
            <View style={styles.arrowRight}><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
        </TouchableOpacity>
    ;


    const DeliveryTime2 =
        <TouchableOpacity
            style={{flexDirection:"row",justifyContent: 'space-between',alignItems:"center",width:"100%",}}
            onPress={
                ()=>{}
            }
        >
            <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"95%"}}>
                <Feather name="clock" size={24} color="black" />
                {/*<Text style={styles.textIconCenter}>Как можно скорее</Text>*/}
                {SelectOption2()}
            </View>
            <View style={styles.arrowRight}><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
        </TouchableOpacity>
    ;



    return (
        <View style={[styles.BoxWhiteBlock,styles.shadow]}>
            <View style={styles.AdressStr}>
                {TypeDeliverySystem ? DeliveryAdresBlock : PickupAdresBlock}
            </View>
            <View style={[styles.AdressStr,{borderTopWidth:1},{paddingTop:15}]}>
                {DeliveryTime}
            </View>
            {DateValueShow !== 0 ?
                <View style={[styles.AdressStr,{borderTopWidth:1},{paddingTop:15}]}>
                    {DeliveryTime2}
                </View>
                : false}
        </View>
    )
};

const styles = StyleSheet.create({
    BoxWhiteBlock:{
        backgroundColor:"#ffffff",
        borderRadius:10,
        paddingHorizontal:10,
        paddingVertical:10,
    },
    AdressStr:{
        width:"100%",
        flexDirection:'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        paddingHorizontal:2,
        paddingVertical:10,
        marginVertical:0,
        borderColor:"#d6d6d6",
        borderTopWidth:0,
        // backgroundColor: "#ff7300"
    },
    textIconCenter:{
        flexDirection:'row',
        // width:"100%",
        maxWidth:"84%",
        marginLeft:5,
        alignItems:'center',
        justifyContent:'space-between',
        fontSize:12,
        textAlign:"left",
        // backgroundColor:"#ff7300"
    },
    arrowRight:{
        width:"5%",
        marginLeft:0,
        paddingTop:Platform.OS === 'ios' ? 0 : 2
    },
    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.22,
        shadowRadius: 40,
        elevation: 3,
        marginBottom:0
    },
});