import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setServiceFeeSum } from "../../store/actions/post";

export const ServiceFee = ({sumOrder=0})=> {
    const ServiceFee = useSelector(state => state.post.ServiceFee);
    const dispatch = useDispatch();
    const [calculatedFee, setCalculatedFee] = useState(0);

    useEffect(() => {
        const recalculateServiceFee = async () => {
            if (!ServiceFee) {
                dispatch(setServiceFeeSum(0));
            console.log('Сервисный сбор отсутствует');
                setCalculatedFee(0);
                return;
            }

            console.log('Сбор есть, считаем');
            let orderTotal = sumOrder || 0;

            let feeValue = parseFloat(ServiceFee.value);
        if (isNaN(feeValue)) {
                dispatch(setServiceFeeSum(0));
            console.log('Ошибка: значение сбора не число');
                setCalculatedFee(0);
                return;
        }

            let feeAmount = ServiceFee.type === 'percent' ? (orderTotal * feeValue / 100) : feeValue;

        if (ServiceFee.min_amount) {
                let minAmount = parseFloat(ServiceFee.min_amount);
            if (!isNaN(minAmount) && feeAmount < minAmount) {
                feeAmount = minAmount;
            }
        }

        if (ServiceFee.max_amount) {
                let maxAmount = parseFloat(ServiceFee.max_amount);
            if (!isNaN(maxAmount) && feeAmount > maxAmount) {
                feeAmount = maxAmount;
            }
        }

        feeAmount = parseFloat(feeAmount.toFixed(0));

        let sumItog = (orderTotal + feeAmount).toFixed(0);
            dispatch(setServiceFeeSum(feeAmount));
            setCalculatedFee(feeAmount);
        };

        recalculateServiceFee();
    }, [ServiceFee, sumOrder, dispatch]); // useEffect срабатывает при изменении ServiceFee или суммы заказа

    console.log('ServiceFee',ServiceFee);

    return <>{calculatedFee}</>;
};
