import React, { useState } from "react";
import {
    View,
    StyleSheet,
    Pressable,
    Text,
    Modal,
    Dimensions,
    ActivityIndicator,
    ScrollView,
    Platform,
    Image
} from "react-native";
import {useDispatch, useSelector} from "react-redux";
import {setListDataDelievryList, setModalStatus, setPreorderData} from "../../../store/actions/post";
import {THEME} from "../../../theme";
import {Ionicons} from "@expo/vector-icons";
import { CheckWorkTimeAffiliateNew } from "../../CheckWorkTimeAffiliateNew";

export const ContentModalTimeDelivery = ({
                                       defaultValue = 0, //Значение по умолчанию valueId
                                       item = [], //список option компонентов (массив value, title)
                                       onChange = ()=>{},
                                       fontSize = 16,
                                       keys = null
                                   }) => {

    const dispatch = useDispatch();
    const listDataDelivery =  useSelector(state=>state.post.listDataDelivery);

    const statusShowDateList = useSelector(state=>state.post.statusLoadServerPreorderData);
    const DateValueShow = useSelector(state=>state.post.preorderData.dateDelivery);
    const AdresList = useSelector(state => state.post.adressAffiliate.list);
    const basket = useSelector(state => state.post.basket);
    const allProducts = useSelector(state => state.post.allProducts);

    const ListAdresForRender = Object.keys(AdresList).map(
        (id,n)=>{
            return AdresList[id];
        }
    );

    const getStartTimesAllPoiunts = ()=>{
        let MinHouseStart = 23;
        ListAdresForRender.forEach((d,n)=>{
            let StartTimes = d.worktime.days[new Date().getDay()].hoursStart;
            if((StartTimes*1) < MinHouseStart){
                MinHouseStart = StartTimes*1;
            }
        });

        return MinHouseStart;
    };

    const getStopTimesAllPoiunts = ()=>{
        let MaxHouseStart = 0;
        ListAdresForRender.forEach((d,n)=>{
            let StopTimes = d.worktime.days[new Date().getDay()].hoursStop;
            if((StopTimes*1) > MinHouseStart){
                MaxHouseStart = StopTimes*1;
            }
        });

        return MaxHouseStart;
    };

    const handleSetStatusModal = (nameModal,status)=>{
        dispatch(setModalStatus(nameModal,status));
    };

    const handleSetDatePreorder = (time)=>{
        let dataReport = {
            dateDelivery: DateValueShow,
            timeDelivery: time
        };
        dispatch(setPreorderData(dataReport));
    };

    const GetModalOption = ()=>{
        let ListOption;

        if(!statusShowDateList){
            ListOption = (
                <Pressable
                    onPress={()=>{
                        handleSetStatusModal("MODAL_TIME_DELIVERY",false);
                    }}
                    key={"Pressable_Select_"+keys+"_loading"}
                >
                    <ActivityIndicator color={THEME.MAIN_COLOR}/>
                </Pressable>
            );
        }else {
            let ArrayList = [];
            let ArrayTime = [];

            if (DateValueShow in listDataDelivery) {
                ArrayTime = listDataDelivery[DateValueShow].timeList;
            }

            Object.keys(ArrayTime).forEach((dates, n) => {
                if (ArrayTime[dates]['false'] === false) {
                    ArrayList.push({title: ArrayTime[dates].title, value: dates});
                }
            });

            // console.info('В корзине это ', basket);
            // console.log('А товары эти есть в наличии ', allProducts);

            // --- Новая проверка запрещённых товаров и категорий ---
            const restrictedCategories = listDataDelivery[DateValueShow]?.categoryStop || [];
            const restrictedProducts = listDataDelivery[DateValueShow]?.productStop || [];
            const blockedItems = [];

            // Текущая дата и время (для ограничения по времени только сегодня)
            const currentDate = new Date().toISOString().split('T')[0]; // Получаем YYYY-MM-DD
            const currentHour = new Date().getHours() + 2; // Часы + 2

            // Проверяем товары в корзине
            basket.forEach(item => {
                const productId = item.id;
                item.type.forEach(product => {
                    const categoryId = product.categoryId;
                    const typeId = product.type_id; // Берём конкретный type_id

                    // if (!productId || !categoryId || !typeId) return; // Если что-то из этого null или undefined, пропускаем товар

                    // Получаем данные о товаре по categoryId и type_id
                    const productData = allProducts[categoryId]?.ProductList?.[productId]?.find(p => p.type_product === typeId);

                    const productName = productData ? productData.title : `Товар ID ${productId}`;
                    const productImage = productData ? productData.img : null; // `img` уже содержит URL
                    // console.warn("ЕБАНЫЕ ДАННЫЕ ТУТ",productData,productName,productImage)

                    // Проверяем, заблокирован ли сам товар
                    const isProductBlocked = restrictedProducts.includes(productId*1);
                    // Проверяем, заблокирована ли категория
                    const isCategoryBlocked = restrictedCategories.includes(categoryId*1);

                    // Добавляем товар в список, если:
                    // - Он сам заблокирован
                    // - Либо его категория заблокирована (но сам товар не в блоке, иначе добавится дважды)
                    if (isProductBlocked || isCategoryBlocked) {
                        blockedItems.push({
                            id: productId,
                            name: productName,
                            image: productImage
                        });
                    }
                });
            });



            // --- Основная логика ---
            // Если есть запрещённые товары/категории в корзине, предзаказ невозможен
            if (blockedItems.length > 0) {
                ListOption = (
                <View style={styles.blockContainer}>
                    <Text style={styles.blockedMessage}>Недоступно для предзаказа на выбранную дату:</Text>
                    <ScrollView style={styles.blockedScrollView}>
                        {blockedItems.map((item, index) => (
                            <View key={index} style={styles.blockedItem}>
                                {item.image ? (
                                    <View style={styles.imageContainer}>
                                        <Image source={{ uri: item.image }} style={styles.productImage} />
                                    </View>
                                ) : null}
                                <Text style={styles.blockedItemText}>{item.name}</Text>
                            </View>
                        ))}
                    </ScrollView>
                </View>
                );
            } else {
                let ArrayList = [];
                let ArrayTime = listDataDelivery[DateValueShow]?.timeList || {};

                Object.keys(ArrayTime).forEach(dates => {
                    let hours = parseInt(ArrayTime[dates].title.split(":")[0]); // Получаем часы из времени

                // Проверяем, совпадает ли дата с сегодняшним днём
                const isToday = DateValueShow === currentDate;

                if (!isToday || hours >= currentHour) { // Ограничение +2 часа только если дата = сегодня
                        if (!ArrayTime[dates]['false'] || restrictedCategories.length > 0 || restrictedProducts.length > 0) {
                            // Если время не запрещено, ИЛИ есть настройка на категории/товары → разрешаем
                            ArrayList.push({ title: ArrayTime[dates].title, value: dates });
                        }
                    }
                });

                if (ArrayList.length === 0) {
                    ListOption = <Text>К сожалению, на данную дату предзаказа нет</Text>;
                } else {
                    ListOption = ArrayList.map((data, n) => (
                        <Pressable
                            key={"Pressable_Select_" + keys + "_" + n}
                            onPress={() => {
                                onChange(n);
                                handleSetStatusModal("MODAL_TIME_DELIVERY", false);
                                handleSetDatePreorder(data.value);
                            }}
                            style={{ flexDirection: "row", justifyContent: "space-between", alignItems: "center" }}
                        >
                            <Text style={styles.optionSelect}>{data.title}</Text>
                        </Pressable>
                    ));
                }
            }
        }

        return (
            <View style={styles.modalBlock} key={"SelectBlock_"+keys}>
                    <View style={styles.modalContent}>
                        <View style={styles.backArrowBlock}>
                            <Pressable
                                onPress={()=>{
                                    handleSetStatusModal("MODAL_TIME_DELIVERY",false);
                                }}
                                style={styles.iconBack}>
                                <Ionicons name="arrow-back" size={28} color="black" />
                            </Pressable>
                            <Text style={styles.textTitleModal}>Время доставки</Text>
                        </View>
                        <ScrollView style={{paddingHorizontal:"10%",paddingTop:"5%"}}>
                            <View style={{flexDirection:"row",justifyContent:"center",alignItems:"center",flexWrap:"wrap"}}>
                                {ListOption !== false ? ListOption : <Text>К сожалению на данную дату предзаказа нет</Text>}
                            </View>
                        </ScrollView>
                    </View>
                </View>
        )
    };

    return (
        <GetModalOption item={item}/>
    );
};

const styles = StyleSheet.create({
    blockedMessage: {
        color: "#262626",
        fontSize: 16,
        textAlign: "center",
        marginBottom: 10
    },
    blockContainer: {
        alignItems: "center",
        paddingVertical: 10
    },
    blockedScrollView: {
        maxHeight: 390, // Чтобы не растягивало экран
        width: "80%",
        paddingHorizontal:"2%"
    },
    blockedItem: {
        flexDirection: "row",
        alignItems: "center",
        backgroundColor: "#f8d7da",
        padding: 10,
        marginBottom: 10,
        borderRadius: 10,
        width: "100%"
    },
    imageContainer: {
        width: 50,
        height: 50,
        marginRight: 10
    },
    productImage: {
        width: "100%",
        height: "100%",
        borderRadius: 5
    },
    blockedItemText: {
        fontSize: 14,
        fontWeight: "bold",
        color: "#721c24"
    },
    container: {
        flex: 1,
        paddingTop: 0,
        alignItems: "flex-start",
        backgroundColor: "#ffffff",
        zIndex:99
    },
    backArrowBlock:{
        width:"100%",
        height:Platform.OS === "android" ? 40 : 60,
        flexDirection:"row",
        alignItems:"center",
        justifyContent:"flex-start"
    },
    iconBack:{
        marginRight:"5%",
        padding: 5
    },
    textTitleModal:{
        fontSize:20,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    defaultValue:{
        textAlign: "left",
        width:"100%",
        paddingHorizontal: "2%",
        paddingVertical:"2%"
    },
    modalBlock:{
        backgroundColor:"#FFFFFF",
        height: "100%",
        width: Dimensions.get('window').width,
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
        paddingTop:"3%"
    },
    modalContent:{
        width:"100%",
        height:"100%",
        backgroundColor:"#ffffff",
        paddingVertical:"4%",
        paddingHorizontal:"3%",
        borderRadius:0
    },
    padding:{
        paddingVertical:"3%",
        paddingHorizontal:"8%",
    },
    optionSelect:{
        width:73,
        marginVertical: "5%",
        marginHorizontal:"3%",
        fontSize:16,
        borderWidth:1,
        borderColor:"#dddddd",
        paddingVertical: "4%",
        paddingHorizontal:"4%",
        borderRadius: 10,
        textAlign:"center"
    },
    optionSelectTed:{
        borderColor:THEME.MAIN_COLOR,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    optionSelect2:{
        fontSize:12,
        borderColor:"#dddddd",
        paddingVertical: "3%",
        color:THEME.MAIN_COLOR
    }
});