import React, { useState, useRef, useEffect } from 'react';
import {
    View,
    Text,
    StyleSheet,
    TextInput,
    Image,
    Pressable,
    Platform,
    SafeAreaView,
    ActivityIndicator,
    Linking
} from 'react-native';
import {ServerDB} from '../serverDb';
import {useDispatch, useSelector} from 'react-redux';
import { HeaderButtons, Item } from 'react-navigation-header-buttons';
import { AppHeaderIcon } from '../components/AppHeaderIcon';
import { THEME } from '../theme';
import {
    setCityUserSystemPhone,
    valinPhoneNumberRu,
    setPhoneNumberDataBase,
    setTimeSendSms,
    setStatusAuthScreen,
    loadAuthInfo
} from '../store/actions/post';
import {ActiveSiteSettings} from "../activeSiteSettings";
import bridge from '@vkontakte/vk-bridge';
import axios from 'axios';


export const AuthorizationScreen = ({ navigation, route }) => {
    const statusLoadingAuthScreen = useSelector(state=>state.post.statusLoadingAuthScreen);
    const AuthTG = useSelector(state => state.post.authTg);


    const padTime = time => {
        return String(time).length === 1 ? `0${time}` : `${time}`;
    };

    const format = time => {
        const minutes = Math.floor(time / 60);
        const seconds = time % 60;
        return `${minutes}:${padTime(seconds)}`;
    };
    const reloadSendSmsTime = 30;

    const [counter, setCounter] = useState(0);
    const [loading, setLoading] = useState(false); // Состояние для прелоадера
    const [loadingVK, setLoadingVK] = useState(false); // Состояние для прелоадера
    useEffect(() => {
        let timer;
        if (counter > 0) {
            timer = setTimeout(() => setCounter(c => c - 1), 1000);
        }

        return () => {
            if (timer) {
                clearTimeout(timer);
            }
        };
    }, [counter]);

    const dispatch = useDispatch();
    const [text, setText] = useState('');

    const PhoneDefault = useSelector(state => state.post.phoneNumberKeyboard);
    const idUserVk = useSelector(state => state.post.idUserVK);

    const [phoneNumberState, setphoneNumberState] = useState(PhoneDefault);
    const [pincodeNumber, setPincodeNumber] = useState(null);
    const loadPageDefault = useSelector(state => state.post.loadPageDefault);

    const [validPhoneNumberStatus, setStstusValinPhoneNumber] = useState(false);
    const [validPincodeStatus, setStatusValinPincode] = useState(true);
    const [validPincodeStatusGreen, setPincodeStatusGreen] = useState(false);
    const [pincodeView, setPincodeView] = useState(false);
    const imgRef = useRef();

    const CityLoadPhone =  useSelector(state => state.post.cityLoadPhone);
    const AuthUserStatus =  useSelector(state => state.post.AuthUserStatus);

    const saveHandler = async (cityId,cityName) => {
        const post = {
            cityId,
            cityName
        };
        await dispatch(setCityUserSystemPhone(post));
        window.location.hash = 'Catalog';
        navigation.navigate('Catalog');
    };

    const validnumberNull = (number = null)=>{
        switch (number) {
            case null: return '+7';
            case '+': return '+7';
            default: return number;
        }
    };

    const checkNumber = (number) => {
        const valinNumber = valinPhoneNumberRu(number);
        setStstusValinPhoneNumber(valinNumber);
        setphoneNumberState(valinNumber);
        dispatch(setPhoneNumberDataBase(validnumberNull(number)));
    };

    const checkPincode = (number) => {
        setPincodeNumber(number);
        if(number.length>3){
            getStatusCodeServer(number);
        }
    };

    const getStatusCodeServer = async (number)=>{
            let resultCheckPinkode = await ServerDB.VerificationCodeServerCall(PhoneDefault, number,idUserVk.id);

            if (resultCheckPinkode.status.result === "ERROR_CODE") {
                setStatusValinPincode(false);
                    setTimeout(()=>{
                        setStatusValinPincode(true);
                        setPincodeNumber(null);
                        },500);
            } else {
                await setStatusValinPincode(true);
                setPincodeStatusGreen(true);
                await dispatch(loadAuthInfo(true,false,false,false,false,idUserVk));
                setPincodeNumber(null);
                setStstusValinPhoneNumber(null);
                setTimeout(()=>{
                    setPincodeStatusGreen(false);
                    setPincodeView(false);
                    dispatch(setPhoneNumberDataBase('+7'));
                },10000);
            }
    };

    const sendPhoneToBot = async (phone, siteUrl, type = 'tg') => {
    console.log(phone, siteUrl);
    let linkBot = '/moduls/AuthTG/obr/setCookieBot.php?setTokenBotTg=1&defouldData=1';
    switch (type) {
        case "tg": linkBot = '/moduls/AuthTG/obr/setCookieBot.php?setTokenBotTg=1'; break;
        case "vk": linkBot = '/moduls/AuthTG/obr/setCookieBot.php?setTokenBotVK=1'; break;
        default:linkBot = '/moduls/AuthTG/obr/setCookieBot.php?setTokenBotTg=1&defouldData=2';
    }

        try {
            const response = await fetch(siteUrl+linkBot, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
            body: `phone=${encodeURIComponent(phone)}&site_url=${encodeURIComponent(siteUrl)}&idVkUser=${encodeURIComponent(idUserVk.id)}`
            });

        const text = await response.text();
        console.log('Response from server:', text); // Логирование полного ответа от сервера

        const data = JSON.parse(text); // Преобразование текста в JSON
            return data;
        } catch (error) {
            console.error('Ошибка при отправке данных на сервер с ботом:', error);
            return { success: false, message: 'Ошибка при отправке данных на сервер с ботом' };
        }
    };

    const generateTokenAndAuth = async () => {
        setLoading(true); // Устанавливаем состояние загрузки
        const phone = PhoneDefault;
        const siteUrl = ActiveSiteSettings.URL; // Используем URL API
        if (phone) {
            const data = await sendPhoneToBot(phone, siteUrl, 'tg');
            if (data.success) {
                // Открываем ссылку в новой вкладке
                Linking.openURL('https://t.me/auth_pixelcrm_bot?start=1');
                checkAuthStatus(data.token,siteUrl); // Запускаем проверку авторизации
            } else {
                setLoading(false); // Отключаем прелоадер в случае ошибки
                alert('Ошибка: ' + data.message);
            }
        } else {
            setLoading(false); // Отключаем прелоадер в случае ошибки
            alert('Пожалуйста, введите ваш номер телефона.');
        }
    };

    const generateTokenAndAuthVK = async () => {
        setLoading(true); // Устанавливаем состояние загрузки
        setLoadingVK(true); // Устанавливаем состояние загрузки
        bridge
            .send('VKWebAppGetPhoneNumber')
            .then(async (data) => {
                if (data.phone_number) {
                    const phone = data.phone_number;
                    const siteUrl = ActiveSiteSettings.URL; // Используем URL API
                    if (phone) {
                        const data = await sendPhoneToBot(phone, siteUrl, 'vk');
                        if (data.success) {
                            // Открываем ссылку в новой вкладке
                            axios
                                .post('https://tghook.ebee.me/vk_webhook.php', {
                                    updateMessageVKBot:{
                                        phone_number: phone,
                                        idUserVK: idUserVk.id
                                    }
                                })
                                .then((response) => {
                                    switch (response.data?.error) {
                                        case true:
                                            setLoading(false); // Отключаем прелоадер в случае ошибки
                                            console.log('Ошибка:', response.data);
                                            break;
                                        case false:
                                            console.log('Нет ошибок:', response.data);
                                            break;
                                        default:
                                            setLoading(false); // Отключаем прелоадер в случае ошибки
                                            console.log('Ошибка есть:', response.data);
                                    }

                                })
                                .catch((error) => {
                                    setLoading(false); // Отключаем прелоадер в случае ошибки
                                    console.error('Error confirming phone:', error)
                                });

                            checkAuthStatus(data.token,siteUrl); // Запускаем проверку авторизации
                        } else {
                            setLoading(false); // Отключаем прелоадер в случае ошибки
                            alert('Ошибка: ' + data.message);
                        }
                    } else {
                        setLoading(false); // Отключаем прелоадер в случае ошибки
                        alert('Пожалуйста, введите ваш номер телефона.');
                    }

                } else {
                    // Пользователь отклонил доступ

                }
            })
            .catch((error) => {
                // Пользователь отклонил доступ
                console.error('Error requesting phone number:', error)
            });
    };

    const checkAuthStatus = (token,siteUrl,timeL = 5000) => {
        const interval = setInterval(async () => {
            const response = await fetch(siteUrl + '/moduls/AuthTG/obr/check_auth_status.php', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
                body: `token=${encodeURIComponent(token)}`
            });

            const data = await response.json();
            if (data.status === 'authorized') {
                clearInterval(interval); // Останавливаем интервал
                await dispatch(loadAuthInfo(true, false, false, false, false, idUserVk)); // Загружаем данные авторизации
                setLoading(false); // Отключаем прелоадер
            }
        }, timeL); // Проверяем статус каждые 5 секунд
    };

    const CityList = useSelector(state => state.post.cityList);
    const CityDatas = useSelector(state => state.post.cityData);

    useEffect(()=> {
        setTimeout(()=>{
            dispatch(setStatusAuthScreen(false));
        },3000);

        if(AuthUserStatus === true){
            window.location.hash = 'Catalog';
            navigation.navigate('Catalog');
        }

    },[dispatch,AuthUserStatus]);

    if(statusLoadingAuthScreen){
        return (
            <SafeAreaView style={[styles.centeredViewWindowTop,{flex:1,alignItems:"center",justifyContent:"center"}]}>
                <ActivityIndicator color={THEME.TEXT_COLOR}  size={120} />
            </SafeAreaView>
        );
    }

    if(!AuthUserStatus){
        if(!loadPageDefault){
            return (
                  <SafeAreaView style={[styles.centeredViewWindowTop,{flex:1,alignItems:"center",justifyContent:"center",paddingBottom:"15%"}]}>
                    <>
                        <ActivityIndicator color="#FFFFFF" size="35%" style={{marginBottom:0,paddingBottom:0}}/>
                        <Text style={{color:'#FFFFFF',fontSize:18,textAlign:"center",paddingTop:0,marginTop:0}}>{"Обновление авторизационных \nданных"}</Text>
                    </>
                  </SafeAreaView>
            );
        }

        if(!pincodeView) {
            return (
                <SafeAreaView style={styles.centeredViewWindowTop}>
                    <View style={styles.modalView}>
                        <Text style={styles.modalText}>Введите номер телефона</Text>
                        <TextInput
                            style={validPhoneNumberStatus ? [styles.phoneInput, styles.phoneInputSuccess] : [styles.phoneInput,styles.phoneInputError]}
                            onChangeText={(text) => checkNumber(text)}
                            maxLength={12}
                            value={PhoneDefault}
                            multiline={false}
                            placeholder={'+79999999999'}
                            textAlign={'center'}
                            keyboardType={Platform.OS === 'android' ? "phone-pad" : "phone-pad"}
                        />
                        {loading ? (
                            <div style={{margin:"10px auto",textAlign:"center"}}>
                            <ActivityIndicator size="large" color={THEME.MAIN_COLOR} />
                                {loadingVK ? false :
                                    <Text style={{textAlign: "center", fontSize: 12, color: "#252525"}}>Если не
                                        произошло автоматическое перенаправление, перейдите в бот <a target="_blank"
                                                                                                     href="https://t.me/auth_pixelcrm_bot?start=1">https://t.me/auth_pixelcrm_bot</a></Text>
                                }
                            </div>
                        ) : (
                            <>
                        <Pressable
                            style={validPhoneNumberStatus ? [styles.buttonCity, styles.buttonsSuccess] : [styles.buttonCity, styles.buttonsError]}
                            disabled={validPhoneNumberStatus ? false : true}
                            color={THEME.MAIN_COLOR}
                            onPress={() => {
                                if(validPhoneNumberStatus){
                                    setPincodeView(true);
                                    dispatch(setTimeSendSms(new Date().getMilliseconds()+(30*1000)));
                                    setCounter(reloadSendSmsTime);
                                    ServerDB.VerificationPhoneServerCall(PhoneDefault);
                                }
                            }}
                        >
                            <Text style={validPhoneNumberStatus ? styles.textSuccess : styles.textError}>Получить код</Text>
                        </Pressable>
                                {AuthTG ? <>
                                <Text>Или</Text>
                        <Pressable
                            style={validPhoneNumberStatus ? [styles.buttonCity, styles.buttonsSuccess] : [styles.buttonCity, styles.buttonsError]}
                            disabled={validPhoneNumberStatus ? false : true}
                            onPress={generateTokenAndAuth}
                        >
                            <Text style={validPhoneNumberStatus ? styles.textSuccess : styles.textError}>Подтвердить номер по Телеграм</Text>
                        </Pressable>
                                    </>
                                    : false}

                                <>
                                    <Text>Или</Text>
                                    <Pressable
                                        style={[styles.buttonCity, styles.buttonsSuccess]}
                                        onPress={generateTokenAndAuthVK}
                                    >
                                        <Text style={styles.textSuccess}>Подтвердить через VK</Text>
                                    </Pressable>
                                </>

                            </>
                        )}
                        <Text style={styles.privacyPolicyText}>
                            Введя номер телефона и нажав "Получить код", вы соглашаетесь с{' '}
                            <Text style={styles.privacyPolicyLink} onPress={() => Linking.openURL(ActiveSiteSettings.URL+'/Privacy_policy')}>
                                политикой конфиденциальности
                            </Text>.
                        </Text>
                    </View>
                    {Platform.OS === 'ios' ?
                        <View>
                            <Pressable
                                style={[styles.buttonCity, styles.buttonsSuccess,{whiteSpace:"nowrap",textAlign:"center",margin:0}]}
                                color={THEME.MAIN_COLOR}
                                onPress={() => {
                                    window.location.hash = 'Catalog';
                                    navigation.navigate('Catalog');
                                }}
                            >
                                <Text style={{color:"#fff",textAlign:"center"}}>Вернуться в каталог</Text>
                            </Pressable>
                        </View>
                        : <View>
                            <Pressable
                                style={[styles.buttonCity, styles.buttonsSuccess,{backgroundColor:"#ffffff",whiteSpace:"nowrap",textAlign:"center",margin:0}]}
                                color={THEME.MAIN_COLOR}
                                onPress={() => {
                                    window.location.hash = 'Catalog';
                                    navigation.navigate('Catalog');
                                }}
                            >
                                <Text style={{color:THEME.MAIN_COLOR,minWidth:170,textAlign:"center"}}>Вернуться в каталог</Text>
                            </Pressable>
                        </View>}
                </SafeAreaView>
            );
        }else{
            return (
                <SafeAreaView style={styles.centeredViewWindowTop}>
                    <View style={styles.modalView}>
                        <View>
                            <Image source={require("../../assets/phone_call_icon.png")}  style={{width:120,height:120}}/>
                        </View>
                        <Text style={styles.modalText}>На введенный номер телефона поступит звонок, введите последние 4 цифры входящего номера.</Text>
                        <TextInput
                            style={
                                validPincodeStatus
                                    ? validPincodeStatusGreen
                                        ? [styles.phoneInput, styles.codeInputSuccess]
                                        : [styles.phoneInput, styles.codeInputDefault]
                                    : [styles.phoneInput, styles.codeInputError]
                            }
                            onChangeText={(text) => checkPincode(text)}
                            maxLength={5}
                            value={pincodeNumber}
                            multiline={false}
                            textAlign={'center'}
                            keyboardType={Platform.OS === 'android' ? "phone-pad" : "phone-pad"}
                        />
                        {validPincodeStatusGreen ?
                            <View>
                                <ActivityIndicator size={29} color={THEME.MAIN_COLOR}/>
                            </View>
                            :
                            <Pressable
                                style={[styles.buttonCity,styles.refreshInputButton]}
                                onPress={() => {
                                    if(counter === 0) {
                                        setPincodeView(false)
                                    }
                                }}
                            >
                                <Text style={styles.textRefreshButton}>Получить код повторно {counter === 0 ? "" : <Text>{"\n"}через {format(counter)} мин</Text>}</Text>
                            </Pressable>
                        }
                    </View>
                    <View>
                        <Pressable
                            style={[styles.buttonCity, styles.buttonsSuccess]}
                            color={THEME.MAIN_COLOR}
                            onPress={() => {
                                window.location.hash = 'Catalog';
                                navigation.navigate('Catalog');
                            }}
                        >
                            <Text style={{color:"#fff"}}>Вернуться в каталог</Text>
                        </Pressable>
                    </View>
                </SafeAreaView>
            );
        }
    }

    if(CityLoadPhone){
    return (
            <SafeAreaView style={styles.centeredViewWindow}>
                <View style={styles.modalView}>
                    <Text style={styles.modalText}>Выберите город</Text>
                    {Object.keys(CityList).map((data,id)=>{
                        return(
                            <Pressable
                                style={styles.buttonCity}
                                onPress={() => {
                                        saveHandler(data,CityList[data]);
                                }}
                                key={"CityLink_"+id}
                            >
                                <Text style={styles.textStyle}>{CityList[data]}</Text>
                            </Pressable>
                        );
                    })}
                </View>
            </SafeAreaView>
        );
    } else {
        return null;
    }

    return null;
};

AuthorizationScreen.navigationOptions = ({ navigation, route }) => ({
    headerShown: false,
    headerTitle: 'Авторизация',
    headerLeft: (
        <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
            <Item
                title='Toggle Drawer'
                iconName='ios-menu'
                onPress={() => navigation.toggleDrawer()}
            />
        </HeaderButtons>
    )
});

const styles = StyleSheet.create({
    centeredViewActive: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
    },
    centeredView: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
    },
    centeredViewWindow: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        backgroundColor:THEME.MAIN_COLOR
    },
    centeredViewWindowTop: {
        flex: 1,
        justifyContent: "flex-start",
        alignItems: "center",
        marginTop: 0,
        paddingTop:"15%",
        backgroundColor:THEME.MAIN_COLOR
    },
    modalView: {
        margin: 20,
        backgroundColor: "white",
        borderRadius: 20,
        padding: 35,
        alignItems: "center",
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        elevation: 5
    },
    button: {
        borderRadius: 20,
        padding: 10,
        elevation: 2
    },
    buttonOpen: {
        backgroundColor: "#F194FF",
    },
    buttonClose: {
        backgroundColor: "#f35025",
    },
    textStyle: {
        color: "white",
        fontWeight: "bold",
        textAlign: "center"
    },
    modalText: {
        marginBottom: 15,
        textAlign: "center"
    },
    LocationName:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontSize:20,
        color:THEME.TEXT_COLOR,
        paddingHorizontal:11
    },
    buttonCity:{
        paddingVertical: 10,
        paddingHorizontal: 25,
        elevation: 2,
        marginVertical:10,
        marginHorizontal:"5%",
        color:THEME.TEXT_COLOR,
        fontFamily: "open-bold",
        fontSize:20,
        backgroundColor:THEME.MAIN_COLOR,
        borderRadius:8,
    },
    phoneInput:{
        borderWidth:0,
        borderColor:'#333333',
        backgroundColor:'#f2f2f2',
        borderRadius:8,
        width:"100%",
        minWidth:170,
        paddingVertical: Platform.OS === 'android' ? 4 : 10,
        paddingHorizontal:5,
        textAlign:'center'
    },
    phoneInputError:{
    },
    phoneInputSuccess:{
    },
    buttonsSuccess:{
        backgroundColor:THEME.MAIN_COLOR,
        opacity:1
    },
    buttonsError:{
        backgroundColor:"#f2f2f2",
        opacity:0.3
    },
    textSuccess:{
        color:THEME.TEXT_COLOR,
        opacity:1
    },
    textError:{
        color:"#333333",
        opacity:0.9
    },
    refreshInputButton:{
        paddingVertical: 0,
        paddingHorizontal: 0,
        elevation: 0,
        marginVertical:10,
        marginHorizontal:"5%",
        color:"#333333",
        fontFamily: "open-bold",
        backgroundColor:"#ffffff",
        borderRadius:0,
        opacity:1
    },
    textRefreshButton:{
        color:"#a8a8a8",
        fontSize:10,
        textAlign:'center',
        lineHeight:20
    },
    codeInputDefault:{
        color:'#333'
    },
    codeInputSuccess:{
        backgroundColor:'#34ff6d',
        color:'#fff'
    },
    codeInputError:{
        backgroundColor:'#ff4234',
        color:'#fff'
    },
    privacyPolicyText: {
        marginTop: 20,
        textAlign: 'center',
        color: '#333',
        fontSize: 14
    },
    privacyPolicyLink: {
        color: THEME.MAIN_COLOR,
        textDecorationLine: 'underline'
    }
});
