import React, { useState, useEffect } from 'react';
import {View, ActivityIndicator, Platform} from 'react-native';
import { WebView } from 'react-native-webview';
import {useDispatch, useSelector} from "react-redux";
import {ServerDB} from "../../serverDb";
import {API_KEY, URL_API, URL_SITE} from "../../store/types";
import {loadAuthInfo, setActiveOrderScreenData, setStatusReloadHelloPoints} from "../../store/actions/post";
import {OnlinePaymentScreen} from "../PaymentOnlineScreen";

const HelloPointsScreen = ({ navigation, route }) => {
    const [htmlContent, setHtmlContent] = useState('');
    const [loading, setLoading] = useState(true);
    const dispatch = useDispatch();
    const CityData = useSelector(state => state.post.cityData);

    const idUserSystem = useSelector(state => state.post.AuthUserData.id);
    const idUserVk = useSelector(state => state.post.idUserVK);

    let dateNews = new Date();

    const URL = URL_API+"HelloPointsAuth&action=showModalApp&apiKey="+API_KEY+"&CityIdSystem="+CityData.cityId+"&idUserSystem="+idUserSystem+"&timeDevice="+dateNews.getMilliseconds();
    let myWebView;
    return  (
        <WebView
            ref={el => (myWebView = el)}
            startInLoadingState={true}
            // renderLoading={renderLoading}
            injectedJavaScriptForMainFrameOnly={true}
            source={{
            uri: URL ,
            headers:{
                'user-agent':'Mozilla/5.0 (iPhone; CPU iPhone OS 13_2_3 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/13.0.3 Mobile/15E148 Safari/604.1'
            }
        }}
                  onMessage={event => {
                    // console.log('Получено сообщение:', event.nativeEvent.data); // Проверка полученных данных
                    if (event.nativeEvent.data !== 'undefined') {
                        try {
                            let e = JSON.parse(event.nativeEvent.data);
                        // console.log('Парсенные данные:', e);

                            if (typeof e === 'object' && 'task' in e) {
                                              var task = e['task'];
                                if (task === 'successHelloPointsReloadLocation') {
                                                  // console.log('Попытка го');
                                                  // alert('Перезагрузка через 2 секунды');
                                                  setTimeout(()=>{
                                    // Для React Native вы должны использовать метод навигации
                                    //                   console.log('Переходим');
                                                      // dispatch(setStatusReloadHelloPoints(true));
                                                      dispatch(loadAuthInfo(true,false,false,false,false,idUserVk));
                                                      // dispatch(loadAuthInfo(true,false, null, null, Platform.OS, idUserAuthVK,null));
                                                      navigation.goBack();
                                                      // navigation.replace('Catalog');
                                                      // Пример
                                                  },2000);
                                }
                            }
                        } catch (error) {
                            // console.error('Ошибка при парсинге данных:', error);
                        }
                    }
                  }}
            />
    )
};

HelloPointsScreen.navigationOptions = ({ navigation, route }) => {

};

export default HelloPointsScreen;
