import React, { useState } from 'react';
import { View, TextInput, Button, StyleSheet, Modal } from 'react-native';
import axios from 'axios';

const HelloPointsModal = ({ isVisible, onClose }) => {
    const [email, setEmail] = useState('');
    const [birthday, setBirthday] = useState('');

    const formatDate = (dateString) => {
        // Убираем все, кроме цифр
        const numericText = dateString.replace(/\D/g, '');

        // Форматируем дату в dd.mm
        if (numericText.length >= 2) {
            const day = numericText.substring(0, 2);
            const month = numericText.substring(2, 4);
            const year = numericText.substring(4, 8); // Год на всякий случай, если введено
            return `${day}${month ? '.' + month : ''}${year ? '.' + year : ''}`; // Форматируем результат
        }
        return numericText; // Возвращаем только цифры
    };

    const handleEmailChange = (text) => {
        setEmail(text);
        // Здесь можно добавить дополнительные проверки, если необходимо
    };

    const handleBirthdayChange = (text) => {
        const formattedDate = formatDate(text);
        setBirthday(formattedDate);
    };

    const validateEmail = (email) => {
        const regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return regex.test(email);
    };

    const validateDate = (dateString) => {
        const regex = /^([0-2][0-9]|3[01])\.([0][1-9]|1[0-2])\.\d{4}$/;
        return regex.test(dateString);
    };

    const handleSubmit = async () => {
        if (!validateEmail(email)) {
            console.log('Ошибка', 'Введите корректный email.');
            return;
        }

        if (!validateDate(birthday)) {
            console.log('Ошибка', 'Дата рождения должна быть в формате dd.mm.yyyy.');
            return;
        }

        try {
            const response = await axios.post('/moduls/HelloPointsAuth/function/obr.php?action=saveData', {
                email,
                birthday,
            });
            const result = response.data;
            if (result.error) {
                console.log('Ошибка', result.errorMessage);
            } else {
                console.log('Успех', 'Данные успешно сохранены.');
                onClose(); // Закрыть модальное окно
            }
        } catch (error) {
            console.log('Ошибка', 'Произошла ошибка при отправке данных.');
        }
    };

    return (
        <Modal isVisible={isVisible}>
            <View style={styles.modalContent}>
                <TextInput
                    placeholder="Дата рождения (dd.mm.yyyy)"
                    value={birthday}
                    onChangeText={handleBirthdayChange}
                    style={styles.input}
                    keyboardType="numeric" // Позволяем вводить только числа
                />
                <TextInput
                    placeholder="Email"
                    value={email}
                    onChangeText={handleEmailChange}
                    style={styles.input}
                />
                <Button title="Сохранить данные" onPress={handleSubmit} />
                <Button title="Отказаться" onPress={onClose} color="#acacac" />
            </View>
        </Modal>
    );
};

const styles = StyleSheet.create({
    modalContent: {
        backgroundColor: '#f9f9f9',
        padding: 20,
        borderRadius: 12,
        alignItems: 'center',
        maxWidth: 500,
        alignSelf: 'center',
    },
    input: {
        borderColor: '#ddd',
        borderWidth: 1,
        borderRadius: 8,
        padding: 12,
        marginBottom: 15,
        width: '100%',
    },
});

export default HelloPointsModal;
