import {GetPriceProductBasketNumber} from "./getPriceProductBasket";

export const GetAllPriceBasketDontTopping = (basket = [],allProducts =[], PromoData = [], returnBolean = false, PromokodeInfoFirstSum = false,PromoTotal)=>{

    if(allProducts === null || PromoData === [] || PromoData === null || PromoData === undefined || PromoData == 'undefined'){
        return false;
    }

    let AllSum = 0;
    let Bolean = false;

    // console.log("PromokodeInfoFirstSum = = = = >>>>>>>",PromokodeInfoFirstSum);
    // console.log("PromoData = = = = >>>>>>>",PromoData);


    let ArrayPriceProduct = [];
//    console.log('Корзина по которой проходимся, вот такая ',basket);
    basket.forEach((productBasket,num)=>{
        productBasket.type.forEach((typeProducts,numbs)=>{

            let product = [];

            if(typeProducts.categoryId in allProducts){
                //Если категорния есть
                if(productBasket.id in allProducts[typeProducts.categoryId]['ProductList']){
                    //Если товар есть
                    if(allProducts[typeProducts.categoryId]['ProductList'][productBasket.id].lenght<=0){
                        //return false;
                    }else{
                        let statusTypeProduct = false;
                        let numberArr = 0;
                        allProducts[typeProducts.categoryId]['ProductList'][productBasket.id].forEach((dataProduct, numb) => {
                            if(typeProducts.type_id == dataProduct['type_product']){
                                //Если такой тип товара есть
                                //Идем по ретурну в конце функции------>>>
                                statusTypeProduct = true;
                                numberArr = numb;
                            }
                        });

                        if(statusTypeProduct === false){
                            // console.log("Неудача ",typeProducts.type_id);
                            //return AllSum;
                        }else{
                            product = {...allProducts[typeProducts.categoryId]['ProductList'][productBasket.id][numberArr],id:productBasket.id};

                            //----->>>>
//                            console.log('Тут product массив = ',product,typeProducts.count);
                            let SumProduct = GetPriceProductBasketNumber(product,typeProducts.count, false, typeProducts?.modifyList);
                            let BoleanDiscount = GetPriceProductBasketNumber(product,typeProducts.count,true, typeProducts?.modifyList,false,PromoData.productInfo.id_product);

                            // let PriceProductFirstPromokodeStep = GetPriceProductPromokodeFirstStep(product,typeProducts.count,PromoData);

                            if(BoleanDiscount === true && returnBolean === true){
                                Bolean = true;
                            }

//                            console.log('Сумма товара для вычисления в панельке = ',SumProduct);

                            AllSum += SumProduct * 1;

                            ArrayPriceProduct.push({
                                idProduct:productBasket.id,
                                idCategoryProduct:typeProducts.categoryId,
                                priceProduct:SumProduct*1
                            });

                        }
                    }

                }
            }
        });
    });

    if(PromoData.dop_settings.type_summa_check === '0'){
        //Не учитываем товар совсем

        // let SumDiscountsMinus = 0;
        //
        // if(PromokodeInfoFirstSum !== false){
        //     PromokodeInfoFirstSum.arrayProductDiscount.forEach((d,n)=>{
        //         console.log('Товары по минусу скидки', d);
        //         // SumDiscountsMinus += (d.tmpData?.ProductListPromokodeDiscount.sumPriceDefOneProduct*1)*(d.count*1);
        //         let statusMinisSum = false;
        //         let SumMinusSumOneProduct = 0;
        //         ArrayPriceProduct.forEach((dd,nn)=>{
        //             if(dd.idProduct == d.id_product && d.tmpData.ProductListPromokodeDiscount.priceProductDiscount*1 > 0){
        //                 statusMinisSum = true;
        //                 SumMinusSumOneProduct = dd.priceProduct;
        //                 console.log('Нужно не считат ьвсю сумму товара ',dd.idProduct, dd.priceProduct);
        //             }
        //         })
        //         if(statusMinisSum === true) {
        //             SumDiscountsMinus += (SumMinusSumOneProduct * 1);
        //         }
        //     });
        //     let AllSum2 =AllSum;
        //     AllSum -=SumDiscountsMinus;
        //
        //     if(AllSum<0){AllSum = 0;}
        //     console.log('Нужно за вычетом скидки от суммы отнять',AllSum2,SumDiscountsMinus);
        // }


        let SumDiscountsMinus = 0;

        if(PromokodeInfoFirstSum !== false){
            PromokodeInfoFirstSum.arrayProductDiscount.forEach((d,n)=>{
                console.log('Товары по минусу скидки 0', d);
                if(d.tmpData?.ProductListPromokodeDiscount.sumProductDiscountFIrstPricePromo*1 >0) {
                    SumDiscountsMinus += (d.tmpData?.ProductListPromokodeDiscount.sumProductDiscountFIrstPricePromo * 1) * (d.tmpData?.ProductListPromokodeDiscount.countProductDiscountBasket * 1);
                    // console.log("0.1",SumDiscountsMinus)
                }else{
                    // SumDiscountsMinus += (d.tmpData?.ProductListPromokodeDiscount.sumProductDiscount * 1) * (d.tmpData?.ProductListPromokodeDiscount.countProductDiscountBasket * 1);
                    SumDiscountsMinus += (d.tmpData?.ProductListPromokodeDiscount.sumProductDiscount * 1) * (d.count*1);
                    // console.log("0.2",SumDiscountsMinus)
                }

                if((PromoTotal>0 || PromoTotal === true) && d.sumPriceDef >d.sumPriceSale){
                    // SumDiscountsMinus +=d.sumPriceSale*(d.count*1)
                    SumDiscountsMinus +=(d.tmpData?.ProductListPromokodeDiscount.sumProductDiscountFIrstPricePromo * 1) * (d.tmpData?.ProductListPromokodeDiscount.countProductDiscountBasket * 1)
                    // console.log("0.3",SumDiscountsMinus)
                }

                if((PromoData?.promokodeInfo?.type === "sale_category" && PromoData?.promokodeInfo?.addition_discount === false)&& d.sumPriceDef>d.sumPriceSale){
                    SumDiscountsMinus +=d.sumPriceSale*(d.count*1)
                    // SumDiscountsMinus +=(d.tmpData?.ProductListPromokodeDiscount.sumProductDiscountFIrstPricePromo * 1) * (d.tmpData?.ProductListPromokodeDiscount.countProductDiscountBasket * 1)
                    // console.log("0.4",SumDiscountsMinus)
                }

                // SumDiscountsMinus += (d.sumPriceAllProductNoDiscount *1);
            });
            let AllSum2 =AllSum;
            AllSum -=SumDiscountsMinus;

            if(AllSum<0){AllSum = 0;}
            // console.log('Нужно за вычетом скидки от суммы отнять',AllSum2,SumDiscountsMinus);
        }
    }

    if(PromoData.dop_settings.type_summa_check === '1'){
        //За вычетом скидки

        // Пример: допустим, у нас есть PromoData.promokodeInfo.applyFromOldPrice
        // если оно отсутствует или false — считаем от акционной цены
        const applyFromOldPrice = PromoData?.promokodeInfo?.applyFromOldPrice === true;

        let SumDiscountsMinus = 0;

        if(PromokodeInfoFirstSum !== false){
            PromokodeInfoFirstSum.arrayProductDiscount.forEach((d,n)=>{
                console.log('=== Товар №', n, '===', d);
                console.log('Старая цена (sumPriceDef):', d.sumPriceDef);
                console.log('Акционная цена (sumPriceSale):', d.sumPriceSale);
                console.log('Кол-во (count):', d.count);

                const promoCount = d.tmpData?.ProductListPromokodeDiscount?.countProductDiscountBasket ?? 0;
                // promoCount – сколько реально идёт под промокод

                // Если вообще нет единиц под промокод, скидки не будет
                if (promoCount <= 0) {
                    console.log(`Товар ${d.id_product} не участвует в промокоде`);
                    return;
                }

                // Сколько шт в корзине всего
                const totalCount = d.count;
                console.log('=== Товар №', n, '===', d, 'всего в корзине:', totalCount, 'из них под промокод:', promoCount);

                // discountBase / discountSale берем из tmpData
                const discountBase  = d.tmpData?.ProductListPromokodeDiscount?.discountOneProductFromBase  ?? 0;
                const discountSale = d.tmpData?.ProductListPromokodeDiscount?.discountOneProductFromSale ?? 0;

                // Выбираем, что именно брать
                const discountOneProduct = applyFromOldPrice ? discountBase : discountSale;

                // == первая часть: скидка только на promoCount
                SumDiscountsMinus += discountOneProduct * promoCount;

                // == Вторая / третья часть == (PromoTotal)
                // если нужно "дважды" применять скидку,
                // остаётся логика точно как у вас,
                // только вместо .discountOneProduct => discountOneProduct
                if((PromoTotal>0 || PromoTotal === true) && d.sumPriceDef>d.sumPriceSale){
                    SumDiscountsMinus += discountOneProduct * promoCount;
                    console.log('Добавили вторую скидку (PromoTotal) для', d.id_product);
                }

                // == третья часть (sale_category)
                if(
                    (PromoData?.promokodeInfo?.type === 'sale_category' &&
                    !PromoData?.promokodeInfo?.addition_discount) &&
                    d.sumPriceDef>d.sumPriceSale
                ){
                    SumDiscountsMinus += discountOneProduct * promoCount;
                    console.log('Добавили скидку (sale_category) для', d.id_product);
                }
            });

            let AllSum2 =AllSum;
            AllSum -=SumDiscountsMinus;
            if(AllSum<0){
                AllSum = 0;
            }
            console.log('Итоговая сумма была', AllSum2, 'вычли', SumDiscountsMinus, 'стало', AllSum);
        }
    }
    //
    //
    // if(PromoData.dop_settings.type_summa_check === '2'){//пофиг
    // }

    if(returnBolean === true) {
        return Bolean;
    }

    return AllSum;
};
