import React, { useState } from "react";
import {
    Modal,
    StyleSheet,
    Text,
    View,
    SafeAreaView,
    Dimensions,
    TouchableOpacity
} from "react-native";
import { AntDesign } from '@expo/vector-icons';
import { useDispatch, useSelector } from "react-redux";
import { setModalStatus } from "../store/actions/post";
import { ButtonEbee } from "../templateComponents/Button/ButtonEbee";
import { THEME } from "../theme";
import ModalPage from "../templateComponents/ModalPage/ModalPage";
import { getTypeDevice } from "../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";

export const PromokodeSuccessModal = () => {
    const dispatch = useDispatch();
    const StatusModal = useSelector(state => state.post.modalStateStatus.MODAL_PROMOKODE_ERROR);
    const PromokodeDate = useSelector(state=>state.post.promokodeData);

    const handleSetStatusModal = (nameModal, status) => {
        dispatch(setModalStatus(nameModal, status));
    };


    const DataPromokodeInfo = ()=>{
        return (
            PromokodeDate !== null && PromokodeDate !== undefined && PromokodeDate != 'undefined' ?
                <View style={styles.BorderRadiusBox}>
                    <Text style={[styles.TextInfoMiniOk,{textAlign: "center"}]}>
                        Ваш промокод: {PromokodeDate?.promokodeInfo?.kode}
                    </Text>
                    <Text style={[styles.TextInfoMiniOk,{textAlign: "center"}]}>
                        {PromokodeDate?.promokodeInfo?.text}
                    </Text>
                </View> :
                <Text style={[styles.TextInfoMiniError,{textAlign: "center"}]}>
                    Ошибка: Не возможно прочитать данные промокода.
                </Text>
        )
    }

    return (
        <ModalPage
            modalVisible={StatusModal}
            ContentModal={
                <View
                    scrollEnabled={false}
                    style={styles.containerContent}>
                    <View style={styles.messageContainer}>
                        <Text style={styles.messageText}>
                            К сожалению активировать промокод по ссыле не удалось. Приносим свои извинения.
                        </Text>
                        <View style={styles.ChildBlock}>
                            {DataPromokodeInfo()}
                        </View>
                    </View>
                    <ButtonEbee
                        onPress={() => {
                            handleSetStatusModal("MODAL_PROMOKODE_ERROR", false);
                        }}
                        size="l"
                        borderRadius={20}
                        shadow={false}
                        style={{width:"120px"}}
                    >
                        Закрыть
                    </ButtonEbee>
                </View>
            }
            HeaderStyle={styles.headerContent}
            ContentModalStyle={styles.Modal}
            HeaderContent={
                <View style={styles.containerHeader}>
                    <AntDesign name="closecircleo" size={20} color={THEME.MAIN_COLOR}
                               onPress={() => {
                                   handleSetStatusModal("MODAL_PROMOKODE_ERROR", false);
                               }}
                               style={styles.closeIcon}
                    />
                </View>
            }
            onClose={() => {
                handleSetStatusModal("MODAL_PROMOKODE_ERROR", false);
            }}
        />
    );
};

const styles = StyleSheet.create({
    containerContent: {
        flex: 1,
        marginTop: getTypeDevice(Dimensions.get('window').width, Dimensions.get('window').height) === false ? 20 : 0,
        paddingHorizontal: "3%",
        paddingVertical: "1%",
        backgroundColor: "#fafafa",
        alignItems: 'center',
        justifyContent: 'center',
    },
    messageContainer: {
        marginBottom: 20,
        paddingHorizontal: 20,
        paddingVertical: 10,
        backgroundColor: "#ffffff",
        borderRadius: 10,
        alignItems: 'center'
    },
    messageText: {
        fontSize: 16,
        color: "#333333",
        textAlign: "center"
    },
    containerHeader: {
        flex: 1,
        alignContent: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        height: 30,
        backgroundColor: "#fafafa",
        borderTopLeftRadius: 10,
        borderTopRightRadius: 10
    },
    closeIcon: {
        alignSelf: 'flex-end',
        paddingTop: 10,
        paddingRight: 15
    },
    headerContent: {
        marginTop: 0,
    },
    Modal: {
        backgroundColor: '#005252',
        marginTop: 0,
    },
    BorderRadiusBox:{
        backgroundColor:"#FFFFFF",
        borderRadius:10,
        overflow:"hidden",
        paddingHorizontal:2,
        paddingVertical:10
    },
    TextInfoMiniOk:{
        backgroundColor:"#FFFFFF",
        fontSize: 14,
        color:"#333333",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    TextInfoMiniError:{
        backgroundColor:THEME.DANGER_COLOR,
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    titleChildBlock:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:16,
        marginBottom:10
    },
    ChildBlock:{
        marginVertical:10
    },
});
